import "./Cards.css";
const Cards = ({ name, img, title, openModal, setChampName }) => {
  const handleName = (e) => {
    setChampName(
      e.currentTarget.lastElementChild.firstElementChild.textContent
    );
    openModal(openModal);
  };

  return (
    <article className="champion__card" onClick={handleName}>
      <img src={img} alt={name} />
      <div className="champ__data">
        <h3>{name}</h3>
        <p>{title}</p>
      </div>
    </article>
  );
};

export default Cards;
