import { useMenu } from "../hooks/useMenu";

import { NavLink } from "react-router-dom";

const ButtonHamburguer = () => {
  const [isActive, toggleClass] = useMenu(false);

  return (
    <>
      <button
        className={`header__menu-btn hamburger hamburger--spin ${
          isActive ? "is-active" : null
        }`}
        type="button"
        onClick={toggleClass}
      >
        <span className="hamburger-box">
          <span className="hamburger-inner"></span>
        </span>
      </button>
      <nav className={`nav__menu ${isActive ? "is-active" : null} `}>
        <NavLink
          exact
          to="/inicio"
          activeClassName="active__link"
          onClick={toggleClass}
        >
          Inicio
        </NavLink>
        <NavLink
          exact
          to="/contacto"
          activeClassName="active__link"
          onClick={toggleClass}
        >
          Contacto
        </NavLink>
      </nav>
    </>
  );
};

export default ButtonHamburguer;
