import { useState, useEffect } from "react";
import Cards from "./Cards";
import Loader from "./Loader";
import "./GetChamps.css";
import { InputChamps } from "./InputChamps";
import Modal from "./Modal";
import { useModal } from "../hooks/useModal";

const GetChamps = () => {
  const [champions, setChampions] = useState([]);
  const [loading, setLoading] = useState(false);
  const [isOpenModal1, openModal1, closeModal1] = useModal(false);
  const [champName, setChampName] = useState(null);

  const URL =
    "http://ddragon.leagueoflegends.com/cdn/11.19.1/data/en_US/champion.json";

  useEffect(() => {
    setLoading(true);
    fetchData(URL);
    setLoading(false);
  }, []);

  const fetchData = async (url) => {
    try {
      const res = await fetch(url),
        json = await res.json();
      let champs = Object.values(json.data);
      setChampions(champs);
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <>
      <InputChamps />
      <div className="container champs__container">
        {loading && <Loader />}
        {champions.length === 0 ? (
          <Loader />
        ) : (
          champions.map((el) => (
            <Cards
              key={el.name}
              setChampName={setChampName}
              name={el.name}
              title={el.title}
              openModal={openModal1}
              img={`http://ddragon.leagueoflegends.com/cdn/img/champion/loading/${el.id}_0.jpg`}
            />
          ))
        )}
        <Modal
          champions={champions}
          isOpen={isOpenModal1}
          closeModal={closeModal1}
          champName={champName}
        />
      </div>
    </>
  );
};

export default GetChamps;
