import React from "react";

const Message = ({ msg }) => {
  let styles = {
    marginBottom: "1rem",
    textAlign: "center",
    fontWeight: "bold",
    color: "#198754",
  };
  return (
    <div style={styles}>
      <p dangerouslySetInnerHTML={{ __html: msg }} />
    </div>
  );
};

export default Message;
