import { useState } from "react";

export const InputChamps = () => {
  const [input, setInput] = useState({});

  const $cards = document.querySelectorAll(".champion__card");

  const handleChange = (e) => {
    setInput({
      ...input,
      [e.target.name]: e.target.value,
    });
    $cards.forEach((el) => {
      if (
        el.lastChild.firstChild.textContent
          .toLowerCase()
          .includes(e.target.value.toLowerCase())
      ) {
        el.classList.remove("filter");
      } else {
        el.classList.add("filter");
      }
    });
  };

  return (
    <div className="input__champs--container">
      <input
        value={input.filter}
        onChange={handleChange}
        type="text"
        name="filter"
        className="input__champs"
        placeholder="Escribe un campeón"
        id="filter"
      />
      <label htmlFor="filter" className="input__champs--label">
        Escribe un campeón
      </label>
    </div>
  );
};
