import { useEffect, useState } from "react";
import Loader from "./Loader";
import "./Modal.scss";
import ModalAbilities from "./ModalAbilities";

const Modal = ({ isOpen, closeModal, champName, champions }) => {
  const [loading, setLoading] = useState(false);
  const [spells, setSpells] = useState([]);

  const handleModalContainerClick = (e) => e.stopPropagation();
  let campeon = {};
  let habilidadesChamp = {};

  for (const key of champions) {
    if (key.name === champName) {
      campeon = key;
    }
  }

  useEffect(() => {
    setLoading(true);
    if (champName !== null) {
      fetchData();
    }
    setLoading(false);
  }, [champName]);

  const fetchData = async () => {
    try {
      if (champName !== null) {
        setLoading(true);

        const res = await fetch(
            `http://ddragon.leagueoflegends.com/cdn/11.20.1/data/en_US/champion/${campeon.id}.json`
          ),
          json = await res.json();
        habilidadesChamp = json.data[campeon.id];
        setSpells(habilidadesChamp);
        setLoading(false);
      }
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <article className={`modal ${isOpen && "is-open"}`} onClick={closeModal}>
      <div className="modal-container" onClick={handleModalContainerClick}>
        <button className="modal-close" onClick={closeModal}>
          X
        </button>
        <div className="modal__content">
          <img
            src={`http://ddragon.leagueoflegends.com/cdn/img/champion/loading/${campeon.id}_0.jpg`}
            alt={campeon.name}
          />
          <div className="modal__data">
            <h3>{campeon.name}</h3>
            <p>{campeon.title}</p>
          </div>
          {loading && <Loader />}

          {spells.length === 0 ? (
            <Loader />
          ) : (
            <ModalAbilities
              passive={spells.passive.name}
              passiveImg={spells.passive.image.full}
              tag={spells.tags[0]}
              spells={spells.spells}
            />
          )}
        </div>
      </div>
    </article>
  );
};

export default Modal;
