const ModalAbilities = ({ passive, passiveImg, tag, spells }) => {
  return (
    <div className="champ__content">
      <p>{tag}</p>
      <div className="abilities__container">
        <div className="abilities__row">
          <div className="abilities__passive">
            <img
              src={`http://ddragon.leagueoflegends.com/cdn/11.20.1/img/passive/${passiveImg}`}
              alt={passive}
            />
          </div>
          {spells.map((el) => {
            return (
              <div className="modal__abilities--container">
                <img
                  key={el.id}
                  src={`http://ddragon.leagueoflegends.com/cdn/11.20.1/img/spell/${el.image.full}`}
                  alt={el.name}
                />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ModalAbilities;
